@import url('https://fonts.googleapis.com/css2?family=Alumni+Sans+Pinstripe&family=Montserrat:wght@300&family=Orbitron&family=Work+Sans:wght@300&display=swap');

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  color: #fff;
  text-align: center;
  height: 1000px;
  /*background: linear-gradient(0.25turn, rgb(78, 211, 255), rgb(158, 158, 158),  #ffa500);*/
  background-color: #000;
}

::selection {
  background-color: rgb(145, 145, 145);
}
.test {
  font-size: 50px;
  color: #fff;
}
a {
  text-decoration: none;
  color: #ffa500;
  transition: color .3s ease-in;
}
a:hover {
  color: rgb(0, 204, 255);
}

.mo{
  font-family: 'Montserrat';
}
.or {
  font-family: 'Orbitron';
}

/*---------------------------header---------------------------*/

.header-container {
  width: 80%;
  margin: 0 auto;
}

.page-header {
  background: #000;
  height: 70px;
}

.page-header::after {
  content: '';
  display: table;
  clear: both;
}

nav {
  float: right;
}

nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

nav li {
  display: inline-block;
  margin-left: 70px;
  padding-top: 26px;

  position: relative;
}

nav a {
  color: #ffa500;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 14px;
}

nav a:hover {
  color: #ffa500;
}

nav a::before {
  content: '';
  display: block;
  height: 5px;
  background-color: #ffa500;

  position: absolute;
  top: 0;
  width: 0%;

  transition: all ease-in-out 250ms;
}

nav a:hover::before {
  width: 100%;
}

.logo {
  height: 55px;
  width: 54px;
  border-radius: 50px;
  float: left;
  margin-left: -10%;
  margin-top: 3.5px;
  user-select: none;
  box-shadow: 4px 3px 0px rgb(255, 255, 255);
  transition: border .3s ease-out;
}
.logo:active {
  transition: all .0s;
  box-shadow: none;
  margin-top: 7px;
  margin-left: -9.5%;
}

/*---------------------------header---------------------------*/
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #000;
}
::-webkit-scrollbar {
  width: 14px;
}
::-webkit-scrollbar-thumb {
  background-color: #F90;
  background-image: -webkit-linear-gradient(45deg,
rgba(255, 255, 255, .2) 25%, transparent 25%, 
transparent 50%, rgba(255, 255, 255, .2) 50%,
rgba(255, 255, 255, .2) 75%, transparent 75%, 
transparent)
}
/*-------------------------------------------------------------*/
.rainbow_text_animated {
  background: linear-gradient(to right, #6666ff, #0099ff, #00ff00, #ff3399, #6666ff);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  animation: rainbow_animation 6s ease-in-out infinite;
  background-size: 400% 100%;
}

@keyframes rainbow_animation {

  0%,
  100% {
    background-position: 0 0;
  }

  50% {
    background-position: 100% 0;
  }
}